import {_decorator, Component, find, game, Game, Mesh, MeshRenderer, Node, profiler, Quat, Size, Vec4} from 'cc';

const {ccclass, property} = _decorator;

class Game_Board {
  public node: Node;
  public grid_size: Size;

  public constructor(node: Node, grid_size: Size) {
    this.node = node;
    this.grid_size = grid_size;
  }

  public init() {}
  public showGrid() {}
  public showAxis() {}
}


@ccclass('Main')
export class Main extends Component {
  game_board: Game_Board;

  start() {
    let game_board_node: Node = find('Game_Board');
    this.game_board = new Game_Board(game_board_node, new Size(10, 10));
  }
}
